body {
  background-color: #111;
  color: #fff;
  font-family: Arial, sans-serif;
  text-align: center;
}

.game {
  padding: 20px;
}

h1 {
  font-size: 28px;
  margin-bottom: 10px;
}


.board-row {
  display: flex;
  justify-content: center;
}

.square {
  width: 70px;
  height: 70px;
  margin: 3px;
  background-color: #222;
  border: 4px solid #00aaff;
  box-sizing: border-box;
  cursor: pointer;

  image-rendering: pixelated;

  display: flex;
  align-items: center;
  justify-content: center;
}

.square:hover {
  background-color: #333;
}

.square.highlight {
  background-color: #335500;
}

.piece {
  width: 60%;
  image-rendering: pixelated;
}

.status {
  margin-bottom: 12px;
  font-size: 20px;
}


button {
  cursor: pointer;
}

.reset-btn,
.theme-btn {
  margin: 8px;
  padding: 6px 12px;
  font-size: 14px;
  border: 2px solid #00aaff;
  background-color: #111;
  color: #fff;
}

.reset-btn:hover,
.theme-btn:hover {
  background-color: #222;
}

.game-info {
  margin-top: 12px;
}

.game-info button {
  background-color: #111;
  color: #fff;
  border: 1px solid #555;
  margin: 4px 0;
  padding: 4px 8px;
}

.game-info button:hover {
  background-color: #222;
}
